html{
  font-size-adjust: 62.5%;
  box-sizing:border-box;

}

body{
  font: 1.6rem Helvectica;
  height: 100vh;
  margin: 0;
}
#root{
  height:100%;
}
.grid-container{
  display: grid;
  grid-template-areas: "header"
  "main"
  "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100% ;
}
.header{
  grid-area: header;
  background-color:burlywood;
  color: black;
  display: flex;
  justify-content: space-between;
  padding-right: 5rem;
}
.brand a{
  color: royalblue;
  font-size: 2.5rem;
  font-weight: bold;
  text-decoration: none;
}
.header-links a{
  color: royalblue;
  text-decoration: none;

}

.headerLinks a:hover{
  color: black;
  text-decoration: none;

}

.main{
  grid-area: main;
}

.footer{
  grid-area: footer;
  background-color: burlywood;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;

}a{
  text-decoration: none;
}
a:hover{
   color: black;
}
/*home screen */

.products{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100%;
}
.products li{
  list-style: none;
  padding: 0;
  flex: 0 1 34rem;
  margin: 1rem;
  height: 50rem;
  border-bottom:.1em brown solid;
}
.product{
  display: flex;
  flex-direction:column;
  justify-content: space-between;
 
}
.product-name{
  font-size:1.5rem;
  color: rgb(65, 15, 146);
  font-weight: bold;
}
.product-brand{
  font-size: 1.2rm;
  color: cornflowerblue;
}
.product-price{
  font-size: 2rem;
  font-weight: bold;
  color: rgb(65, 15, 146);
}

.product-image{
  max-width: 34rem;
  max-height: 34rem;
}

/*sidebar */
.brand button {
   font-size: 3rem;
   padding: .5rem;
   background: none;
   border: none;
   color: white;
   cursor: pointer;
}
.sidebar{
   position: fixed;
   transition: all .8se;
   transform: translateX(-25rem);
   width: 25rem;
   background-color: grey;
   height: 100%;
}
.sidebar.open{
   transform: translateX(0);
}
.sidebar-close-button{
   border-radius: 50%;
   border : .1rem black solid;
   width: 3rem;
   height: 3rem;
   font-size: 2rem;
   cursor: pointer;
   position: absolute;
   right: .5rem;
   top: 1.5rem;
}
/*Product Details */
.details{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1rem;

}

.details-image{
  flex: 2 1 60rem;
}

.details-image img{
  max-width: 60 rem;
  widows: 100%;
}

.details-info{
  flex: 1 1 30rem;;
}

.details-action{
  flex: 1 1 30rem;
  border: .1rem grey solid;
  border-radius: 0.5rem;
  background-color: cornsilk;
  padding: 1rem;
}
.details-info ul, .details-action ul{
  list-style-type: none;
  padding: 0;
}

.details-action ul li:last-child{
  display: flex;
  flex-direction: column;
}
.details-info li, .details-action li{
  margin: 1rem;
}
.back-to-result{
  padding: 1rem;
}
.button{
padding: 1rem;
border: .1rem grey;
cursor: pointer;
border-radius: 0.5rem;

}
.button:hover{
  border: .1rem black;
}
.button.primary{
  background-color: goldenrod;
}